* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

a {
  text-decoration: none;
}


.content-main {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.wrapper {
  position: absolute;
  width:100%;
  height:100%;
  overflow: auto;
  z-index:5;
  padding-left:150px;
  left:100%;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.wrapper.active {
  left:0;
}

.wrapper.active {
  z-index:10;
}

.wrapper .header {
  background:#000000;
  background-image: url('./Assets/Images/profile-header.png') !important;
  padding:30px 0;
  text-align: center;
}
.wrapper .header h1 {
  color:#ffffff;
}
.wrapper .container {
  position: relative;
  width:80%;
  max-width: 980px;
  padding:40px 0 20px;
  margin:0 auto;
  z-index:5;
  
}
.wrapper > .container {
  background-image: url('./Assets/Images/content-bg.png') !important;
}
.wrapper .container.full {
  width:100%;
  max-width: 100%;
  padding:0;
}


@media screen and (max-width:640px) {
  .wrapper {
      padding-left:0;
  }
  .wrapper .container {
      width:90%;
  }
  
}
@media screen and (max-width:480px) {
  .wrapper .header h1 {
    margin:0;
    font-size:2em;
  }
  
  
}


